'use client';

import { notFound } from "next/navigation";
import Image from "next/image";
import { getPhotographerWithLikes, getAllMediasForPhotographer } from "../../lib/prisma-db.js";
import { useState } from "react";
import styles from '../../page.module.css';

export default async function PhotographerPage({ params }) {
  const resolvedParams = await params;
  const { id } = resolvedParams;

  if (!id) notFound();

  const photographer = await getPhotographerWithLikes(Number(id));
  if (!photographer) notFound();

  const allMedias = await getAllMediasForPhotographer(Number(id));

  // Hook pour le tri côté client
  const [sortBy, setSortBy] = useState("popularity");
  const [medias, setMedias] = useState(allMedias);

  const handleSortChange = (e) => {
    const value = e.target.value;
    setSortBy(value);

    let sortedMedias = [...allMedias];

    if (value === "popularity") {
      sortedMedias.sort((a, b) => b.likes - a.likes);
    } else if (value === "date") {
      sortedMedias.sort((a, b) => new Date(b.date) - new Date(a.date));
    } else if (value === "title") {
      sortedMedias.sort((a, b) => a.title.localeCompare(b.title));
    }

    setMedias(sortedMedias);
  };

  return (
    <div className={styles.container}>
      <header className={styles.header}>
        <Image
          src="/assets/logo/logo-fisheye.png"
          alt="Logo FishEye"
          width={300}
          height={300}
          className={styles.imageHeader}
        />
      </header>

      <div className={styles.infoContainer}>
        <section className={styles.details}>
          <h1>{photographer.name}</h1>
          <p className={styles.localisation}>{photographer.city}, {photographer.country}</p>
          <p>{photographer.tagline}</p>
        </section>

        <section className={styles.contact}>
          <button>Contactez-moi</button>
        </section>

        <section className={styles.portrait}>
          <div className={styles.photoWrapper}>
            <Image
              src={`/assets/${photographer.portrait}`}
              alt={photographer.name}
              fill
              style={{ objectFit: 'cover', objectPosition: 'center' }}
            />
          </div>
        </section>
      </div>

      <div className={styles.sortContainer}>
        <label>Trier par</label>
        <select value={sortBy} onChange={handleSortChange}>
          <option value="popularity">Popularité</option>
          <option value="date">Date</option>
          <option value="title">Titre</option>
        </select>
      </div>

      <div className={styles.gallery}>
        {medias.map(media => (
          <div key={media.id} className={styles.mediaCard}>
            {media.image && (
              <Image
                src={`/assets/${media.image}`}
                alt={media.title}
                width={250}
                height={250}
                style={{ objectFit: 'cover', borderRadius: '5px' }}
              />
            )}
            {media.video && (
              <video width={250} height={250} controls>
                <source src={`/assets/${media.video}`} type="video/mp4" />
              </video>
            )}
            <div className={styles.mediaInfo}>
              <p>{media.title}</p>
              <p>{media.likes} ♥</p>
            </div>
          </div>
        ))}
      </div>

      <div className={styles.photographerCardBottom}>
        <p>{photographer.totalLikes} ♥</p>
        <p>{photographer.price}€ / jour</p>
      </div>
    </div>
  );
}
